import { useQueries } from "@tanstack/react-query"
import { getContentPackageByID } from "../services/getContentPackageByID"
import { contentPackagesByIDQueryKeys } from "../utils/contentPackagesByIDQueryKeys"

function useContentPackagesByID(ids: Array<string>) {
  return useQueries({
    queries: ids.map(id => ({
      queryKey: contentPackagesByIDQueryKeys.packageById(id),
      queryFn: () => getContentPackageByID({ id }),
    })),
  })
}

export { useContentPackagesByID }
